'use client';
import { useState } from 'react';
import { FiUser, FiMail, FiLock, FiPhone, FiArrowRight, FiLogIn, FiEye, FiEyeOff } from 'react-icons/fi';
import { Input, Button } from '@/Component/UI/ReusableCom';
import Link from 'next/link';
import Router from 'next/router';

export default function Register() {
    const [formData, setFormData] = useState({ 
        username: '', 
        email: '', 
        password: '', 
        contactNo: '' 
    });
    const [showPassword, setShowPassword] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [message, setMessage] = useState(null);

    const handleRegister = async (e) => {
        e.preventDefault();
        setMessage(null);
        setIsLoading(true);

        try {
            const res = await fetch('/api/admin/auth/register', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(formData),
            });

            const data = await res.json();

            if (res.ok) {
                setMessage({ 
                    text: data.message || 'Registration successful! Redirecting...', 
                    type: 'success' 
                });
                setTimeout(() => {
                    window.location.href = '/administration/login';
                }, 2000);
            } else {
                setMessage({ 
                    text: data.error || 'Something went wrong. Please try again.', 
                    type: 'error' 
                });
            }
        } catch (error) {
            console.error("Registration error:", error);
            setMessage({ 
                text: "Failed to register. Please check your connection and try again.", 
                type: 'error' 
            });
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-primary-50 to-primary-100 p-4 sm:p-6">
            <div className="w-full max-w-md">
                <div className="bg-white rounded-xl shadow-xl overflow-hidden">
                    {/* Header */}
                    <div className="bg-gradient-to-r from-primary-600 to-primary-500 p-6 text-center">
                        <div className="flex justify-center mb-3">
                            <div className="bg-white/20 p-2 rounded-full">
                                <FiUser className="h-6 w-6 text-white" />
                            </div>
                        </div>
                        <h1 className="text-xl font-bold text-white">Create Account</h1>
                        <p className="text-primary-100 text-sm mt-1">Get started in just a minute</p>
                    </div>
                    
                    {/* Form */}
                    <form onSubmit={handleRegister} className="p-6 space-y-4">
                        {/* Username */}
                        <Input
                            label="Username"
                            type="text"
                            placeholder="johndoe"
                            value={formData.username}
                            onChange={(e) => setFormData({...formData, username: e.target.value})}
                            required
                            icon={FiUser}
                        />
                        
                        {/* Email */}
                        <Input
                            label="Email Address"
                            type="email"
                            placeholder="you@example.com"
                            value={formData.email}
                            onChange={(e) => setFormData({...formData, email: e.target.value})}
                            required
                            icon={FiMail}
                        />
                        
                        {/* Password */}
                        <div className="space-y-1">
                            <div className="flex justify-between items-center">
                                <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                                    Password
                                </label>
                                <button 
                                    type="button"
                                    onClick={() => setShowPassword(!showPassword)}
                                    className="text-xs text-primary-600 hover:text-primary-800 transition-colors"
                                >
                                    {showPassword ? (
                                        <span className="flex items-center">
                                            <FiEyeOff className="mr-1" /> Hide
                                        </span>
                                    ) : (
                                        <span className="flex items-center">
                                            <FiEye className="mr-1" /> Show
                                        </span>
                                    )}
                                </button>
                            </div>
                            <Input
                                id="password"
                                type={showPassword ? 'text' : 'password'}
                                placeholder="••••••••"
                                value={formData.password}
                                onChange={(e) => setFormData({...formData, password: e.target.value})}
                                required
                                minLength="8"
                                icon={FiLock}
                            />
                        </div>
                        
                        {/* Contact Number */}
                        <Input
                            label="Phone Number (Optional)"
                            type="tel"
                            placeholder="+1 (555) 123-4567"
                            value={formData.contactNo}
                            onChange={(e) => setFormData({...formData, contactNo: e.target.value})}
                            icon={FiPhone}
                        />
                        
                        {/* Terms and Conditions */}
                        <div className="flex items-start">
                            <div className="flex items-center h-5">
                                <input
                                    id="terms"
                                    name="terms"
                                    type="checkbox"
                                    className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded"
                                    required
                                />
                            </div>
                            <div className="ml-3 text-sm">
                                <label htmlFor="terms" className="font-medium text-gray-700">
                                    I agree to the{' '}
                                    <a href="#" className="text-primary-600 hover:text-primary-500">
                                        Terms and Conditions
                                    </a>
                                </label>
                            </div>
                        </div>
                        
                        {/* Submit Button - Now visible and properly positioned */}
                        <div className="pt-2">
                            <Button 
                                type="submit" 
                                variant="primary"
                                size="md"
                                fullWidth
                                loading={isLoading}
                            >
                                {isLoading ? 'Creating Account...' : (
                                    <span className="flex items-center justify-center">
                                        Register Now <FiArrowRight className="ml-2 h-4 w-4" />
                                    </span>
                                )}
                            </Button>
                        </div>
                        
                        {/* Message Alert */}
                        {message && (
                            <div className={`p-3 rounded-lg text-sm ${message.type === 'success' ? 'bg-success-50 text-success-700' : 'bg-danger-50 text-danger-700'}`}>
                                <div className="flex items-center">
                                    <div className="flex-shrink-0">
                                        {message.type === 'success' ? (
                                            <svg className="h-5 w-5 text-success-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                                                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                                            </svg>
                                        ) : (
                                            <svg className="h-5 w-5 text-danger-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                                                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                                            </svg>
                                        )}
                                    </div>
                                    <div className="ml-3">
                                        <p>{message.text}</p>
                                    </div>
                                </div>
                            </div>
                        )}
                    </form>
                    
                    {/* Footer */}
                    <div className="px-6 py-4 bg-gray-50 border-t border-gray-100 text-center">
                        <p className="text-sm text-gray-600">
                            Already have an account?{' '}
                            <Link 
                                href="/administration/login" 
                                className="font-medium text-primary-600 hover:text-primary-500 inline-flex items-center group"
                            >
                                <span>Sign in</span>
                                <FiLogIn className="ml-1 h-4 w-4 transform group-hover:translate-x-1 transition-transform" />
                            </Link>
                        </p>
                    </div>
                </div>
                
                {/* Copyright - Smaller and less prominent */}
                <p className="mt-4 text-center text-xs text-gray-500">
                    &copy; {new Date().getFullYear()} DSCI. All rights reserved.
                </p>
            </div>
        </div>
    );
}